.members {
    position: absolute;
    left: 10%;
    right: 10%;
    top: 15%;
    bottom: 10%;
    padding-top: 5%;
    color: white;
    z-index: 150;
}

.members .member:first-child {
    position: absolute;
    width: 15%;
    left: 0%;
    top: 0%;
}

.members .member:nth-child(2) {
    position: absolute;
    width: 15%;
    left: 42.5%;
    top: 0%;
}

.members .member:nth-child(3) {
    position: absolute;
    width: 15%;
    left: 85%;
    top: 0%;
}

.members .member:nth-child(4) {
    position: absolute;
    width: 15%;
    left: 20%;
    top: 50%;
}

.members .member:nth-child(5) {
    position: absolute;
    width: 15%;
    left: 65%;
    top: 50%;
}

.member {
    color: white;
    margin-right: 10%;
}

.member img {
    width: 100%;
    height: 100%;
}

.carousel-caption {
    position: absolute;
    top: 5%;
    left: 5%;
    width: 10%;
}

.carousel-indicators {
    position: fixed;
    bottom: 5%;
}

.carousel-item {
    position: fixed;
    left: 0px;
    top: 0px;
    right: 0px;
    bottom: 0px;
}