body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}


#playBtn {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 100;
  font-size: 60px;
  color: #bdc3c7;
  cursor: pointer;
}

.mouseEnter {
  color: white;
}

#indazooLogo {
  width: 150px;
  height: 80px;
  z-index: 4;
}

#youtube {
  background-color: black;
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  padding: 3%;
  z-index: 10;
}

#closeBtn {
  position: fixed;
  font-size: 20px;
  color: #bdc3c7;
  top: 2%;
  right: 2%;
  z-index: 30;
}

.indazooTitle {
  position: relative;
  top: 0px;
  left: 0px;
}

.indazooLogo {
  position: fixed;
  width: 10%;
  height: 10%;
  left: 10%;
  bottom: 10%;
  z-index: 5;
}

#iconGroup {
  position: fixed;
  bottom: 10%;
  right: 5%;
  z-index: 6;
}

#appMenu a {
  color: #2d3436;
  font-size: 20px;
  padding-right: 20px;
  cursor: pointer;
}

#appMenu a:last-child {
  padding-right: 0;
}

a:hover {
  text-decoration: none;
}

.carouselImg {
  /* Set rules to fill background */
  min-height: 100%;
  min-width: 1024px;
    
  /* Set up proportionate scaling */
  width: 100%;
  height: auto;
    
  /* Set up positioning */
  position: fixed;
  top: 0;
  left: 0;
}

.carousel-control-prev-icon {
  position: fixed;
  left: 5%;
  top: 50%; 
}

.carousel-control-next-icon {
  position: fixed;
  right: 5%;
  top: 50%; 
}

@media screen and (max-width: 1024px) { /* Specific to this particular image */
  .carouselImg {
    left: 50%;
    margin-left: -512px;   /* 50% */
  }
  
}
#mainGroup {
    position: fixed;
    top: 10%;
    right: 5%;
    z-index: 6;
}
  
#mainMenu a {
    color: white;
    font-size: 20px;
    padding-right: 20px;
    cursor: pointer;
}

.clickedMenu {
    color: #2d3436;
}
.members {
    position: absolute;
    left: 10%;
    right: 10%;
    top: 15%;
    bottom: 10%;
    padding-top: 5%;
    color: white;
    z-index: 150;
}

.members .member:first-child {
    position: absolute;
    width: 15%;
    left: 0%;
    top: 0%;
}

.members .member:nth-child(2) {
    position: absolute;
    width: 15%;
    left: 42.5%;
    top: 0%;
}

.members .member:nth-child(3) {
    position: absolute;
    width: 15%;
    left: 85%;
    top: 0%;
}

.members .member:nth-child(4) {
    position: absolute;
    width: 15%;
    left: 20%;
    top: 50%;
}

.members .member:nth-child(5) {
    position: absolute;
    width: 15%;
    left: 65%;
    top: 50%;
}

.member {
    color: white;
    margin-right: 10%;
}

.member img {
    width: 100%;
    height: 100%;
}

.carousel-caption {
    position: absolute;
    top: 5%;
    left: 5%;
    width: 10%;
}

.carousel-indicators {
    position: fixed;
    bottom: 5%;
}

.carousel-item {
    position: fixed;
    left: 0px;
    top: 0px;
    right: 0px;
    bottom: 0px;
}
